<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">{{$route.query.type == 1 ? '班级荣誉详情' : '表扬栏详情'}}</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="$router.push({path:'/editHonor',query:{type:$route.query.type,id:$route.query.id}})">编辑</span>
        </div>
        <div class="c333 f28" style="padding:0.84rem 0 1rem;">
            <div class="nameBox">
                <img :src="data.coverImage" alt="">
            </div>
            <div class="content b_line">
                <p class="subtitle">{{data.title}}</p>
                <p class="c666">{{$route.query.type == 1 ? data.honourDesc : data.mienDesc }}</p>
            </div>
            <div class="content">
                <p class="subtitle">图片</p>
                <div class="picture">
                    <img :src="$api.fileUrl + item" alt="" v-for="(item,index) in data.imageUrl" :key="index" @click="show(item)">
                    <!-- <img src="../../../assets/message/onShow.png" alt=""> -->
                </div>
                <p class="subtitle">视频</p>
                <div class="picture">
                    <video :src="$api.fileUrl + item" v-for="(item,index) in data.videoUrl" :key="index"></video>
                </div>
            </div>  
        </div>
    </div>
</template>

<script>
import { ImagePreview } from 'vant';
export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            title:'',
            data:{}
        }
    },
    methods:{
        returns(){
            this.$router.go(-1);//返回上一层
        },
        show(url){
            ImagePreview([this.$api.fileUrl + url]);
        },
        getHonorDetail(){//获取荣誉详情
            this.$http({url:this.$api.class.getHonorDetail},{id:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    data.data.coverImage = this.$api.fileUrl + data.data.coverImage;
                    this.data = data.data;
                }
            })
        },
        getMienDetail(){//获取风采表情栏详情
            this.$http({url:this.$api.class.getMienDetail},{id:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    data.data.coverImage = this.$api.fileUrl + data.data.coverImage;
                    this.data = data.data;
                }
            })
        }
    },
    created() {
        var type=this.$route.query.type;
        if(type==1){
            this.getHonorDetail();
        }else{
            this.getMienDetail();
        }
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .nameBox{
        padding:.3rem;
    }
    .nameBox img{
        width:100%;
        box-shadow: 0 0 0.3rem #ccc;
    }
    .name{
        border:0;
        text-align: right;
        width:50%;
    }
    .content{
        padding:0 .3rem .3rem;
    }
    .subtitle{
        line-height: .9rem;
    }
    .content textarea{
        width:100%;
        height:2.67rem;
        border:0;
    }
    .picture img{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        margin-left: 0.1rem;
    }
    .picture img:first-child{
        margin-left: 0;
    }
    .picture video{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
        margin-left: 0.1rem;
    }
    .picture video:first-child{
        margin-left: 0;
    }
</style>




